.Header {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    background-color: #0f273d;
    width: 100%;
    height: 80px;
  }
  
  .Header-logo {
    position: absolute;
    top: 30px;
    left: 20px;
    width: 90px;
  }
  
  @media (max-width: 700px) {
    .Header-logo {
      display: none;
    }
  }
  


